import { mapState, mapActions } from 'vuex'
import API_URLS from 'config/API_URLS'

export default {
	data() {
		return {
			errors: [],
		}
	},
	methods: {
		...mapActions('api', [
			'post',
		]),
		register() {
			if (!this.isInput) return;
			this.errors = [];
			this.isSuccess = false;

			this.post({
				url: API_URLS[this.formName].create,
				params: this.postData,
				success: (res) => {
					if (res.result_code == 0) {
						if (res.errors) res.errors.forEach(item => {
								this.errors.push(item.message)
							});
						this.isSuccess = false;
					} else if (res.result_code == 1) {
						this.resetForm();
						this.isSuccess = true;
					}
				},
				error: (err) => {
					this.errors.push('通信に失敗しました。時間を置いてもう一度お試しください。');
				}
			});
		},
		resetForm() {
			this.formItems.forEach ((item) => {
				this[item.name] = '';
			});
		},
	},
	computed: {
		isInput() {
			const notInputItem = this.formItems.find((item) => {
				return item.required && !this[item.name];
			});
			return !notInputItem;
		},
		postData() {
			let data = {};
			this.formItems.forEach((item) => {
				data[item.name] = this[item.name];
			});
			return data;
		}
	},
}
