<template>
	<div class="skillmap">
		<table class="skillmap__table">
			<thead>
				<tr class="skillmap__row">
					<th class="skillmap__column --row_head --col_head --first --sticky --skill">
						<div class="skillmap__columnContent --row_head --col_head --first">大カテゴリ名</div>
					</th> 
					<th class="skillmap__column --row_head --col_head --second --sticky --skill">
						<div class="skillmap__columnContent --row_head --col_head --second">小カテゴリ名</div>
					</th>
					<th class="skillmap__column --row_head --col_head --third --sticky --skill">
						<div class="skillmap__columnContent --row_head --col_head --third">スキル名</div>
					</th>
					<td
						v-for="member in filteredMembers"
						:key="member.id"
						class="skillmap__column --row_head --col_body --sticky"
						@click="updateSortStatus(member.id, 'MemberId', 'vertical')"
					>
						<div class="skillmap__columnContent --row_head --col_body">
							<p>
								{{ member.name }}
								<span
									v-if="selectedMemberId == member.id && sort.vertical.label"
									class="skillmap__sortArrow"
								>{{ sort.vertical.label }}</span>
							</p>
						</div>
					</td>
				</tr>
			</thead>
			<tbody> 
				<tr
					v-for="skill in filteredSkills"
					:key="skill.id"
					class="skillmap__row"
				>
					<th
						v-if="skill.rowspanLarge"
						class="skillmap__column --row_body --col_head --first --sticky --skill"
						:rowspan="skill.rowspanLarge.count"
					>
						<div class="skillmap__columnContent --row_body --col_head --first">
							<p>{{ skill.skill_small_category.skill_large_category.name }}</p>
						</div>
					</th>
					<th
						v-if="skill.rowspanSmall"
						class="skillmap__column --row_body --col_head --second --sticky --skill"
						:rowspan="skill.rowspanSmall.count"
					>
						<div class="skillmap__columnContent --row_body --col_head --second">
							<p>{{ skill.skill_small_category.name }}</p>
						</div>
					</th>
					<th
						class="skillmap__column --row_body --col_head --third --sticky --skill"
						@click="updateSortStatus(skill.id, 'SkillId', 'horizontal')"
					>
						<div class="skillmap__columnContent --row_body --col_head --third">
							<p>
								{{ skill.name }}
								<span
									v-if="selectedSkillId == skill.id && sort.horizontal.label"
									class="skillmap__sortArrow"
								>{{ sort.horizontal.label }}</span>
							</p>
						</div>
					</th>
					<td
						v-for="member in filteredMembers"
						:key="member.id"
						class="skillmap__column --row_body --col_body"
						:class="'--lv' + member.skillLevel[skill.id]"
					>
						<div class="skillmap__columnContent --row_body --col_body">
							{{ member.skillLevel[skill.id] || '-' }}
						</div>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex'
	import common_sort    from 'components/mixins/skillmap/index/sort'
	import common_members from 'components/mixins/skillmap/index/members'

	const sort = {
		data: function() {
			return {
				selectedMemberId : null,
				selectedSkillId  : null,
				sort : {
					vertical   : { id : 0, label : '' },
					horizontal : { id : 0, label : '' }
				},
				sortConfig : {
					vertical : {
						compareFunction : {
							0 : false,
							1 : (a, b) => { return this.compareVertical(a, b); },
							2 : (a, b) => { return this.compareVertical(b, a); },
						},
						label : { 0 : '', 1 : '▲', 2 : '▼' }
					},
					horizontal : {
						compareFunction : {
							0 : false,
							1 : (a, b) => { return this.compareHorizontal(a, b); },
							2 : (a, b) => { return this.compareHorizontal(b, a); },
						},
						label : { 0 : '', 1 : '◀', 2 : '▶' }
					}
				}
			};
		},
		methods: {
			sortTable: function(target, type) {
				const sortId = this.sort[type].id;
				const compareFunction = this.sortConfig[type].compareFunction[sortId];
				if (compareFunction) target.sort(compareFunction);
			},
			updateSortStatus: function(id, target, type) {
				const old = this['selected' + target];
				this['selected' + target] = id;

				const config = this.sortConfig[type];
				const sort   = this.sort[type];
				let sortId = sort.id;

				if (sortId != 0 && old != id) return;

				if (++sortId > 2) sortId = 0;
				sort.id    = sortId;
				sort.label = config.label[sortId];
			},
			compareVertical: function(a, b) {
				let member = this.getMemberById(this.selectedMemberId);
				let comparison;
				
				let alv = member.skillLevel[a.id];
				let blv = member.skillLevel[b.id];
				if (isNaN(alv)) alv = -1;
				if (isNaN(blv)) blv = -1;
				
				/* 大カテゴリ > 小カテゴリ > スキル名 の順番でソートする */
				let nameA = alv + a.skill_small_category.skill_large_category.name + a.skill_small_category.name + a.name;
				let nameB = blv + b.skill_small_category.skill_large_category.name + b.skill_small_category.name + b.name;

				return this.compareName(nameB, nameA);
			},
			compareHorizontal: function(a, b) {
				let id = this.selectedSkillId;
				let comparison;

				let alv = a.skillLevel[id];
				let blv = b.skillLevel[id];
				if (isNaN(alv)) alv = -1;
				if (isNaN(blv)) blv = -1;

				let nameA = alv + a.name;
				let nameB = blv + b.name;

				return this.compareName(nameB, nameA);
			},
		}
	}

	const members = {
		methods : {
			filterMembers : function(members) {
				return members.filter((member) => {
					let isAnuual = this.selectedAnnual ?
						member.annual  == this.selectedAnnual : true;
					let isTeam   = this.selectedTeam   ?
						member.team_id == this.selectedTeam   : true;
					return isAnuual && isTeam;
				});
			},
		},
		computed: {
			filteredMembers : function() {
				let members = this.members;
				members = this.filterMembers(members);
				this.sortTable(members, 'horizontal');
				return members;
			},
		},
	}

	const skills = {
		methods: {
			filterSkills : function(skills) {
				let sssc = this.selectedSkillSmallCategory;
				let sslc = this.selectedSkillLargeCategory;
				return skills.filter((skill) => {
					let isSc = sssc ? skill.skill_small_category.id == sssc : true;
					let isLc = sslc ? skill.skill_small_category.skill_large_category.id == sslc : true;
					return isSc && isLc;
				});
			},
			addRowspan: function(skills) {
				let currrentSmallId;
				let currrentLargeId;
				let currrentRowspanSmall;
				let currrentRowspanLarge;
				const rowspan = { count : 0 };

				skills.map((skill) => {
					this.$delete(skill, 'rowspanSmall');
					this.$delete(skill, 'rowspanLarge');
					
					if (currrentSmallId != skill.skill_small_category.id) {
						currrentRowspanSmall = Object.assign({}, rowspan);
						this.$set(skill, 'rowspanSmall', currrentRowspanSmall);
					}
					if (currrentLargeId != skill.skill_small_category.skill_large_category.id) {
						currrentRowspanLarge = Object.assign({}, rowspan);
						this.$set(skill, 'rowspanLarge', currrentRowspanLarge);
					}

					currrentRowspanSmall.count++;
					currrentRowspanLarge.count++;

					currrentSmallId = skill.skill_small_category.id;
					currrentLargeId = skill.skill_small_category.skill_large_category.id;
				});
			}
		},
		computed: {
			filteredSkills : function() {
				let skills = this.skills;
				skills = this.filterSkills(skills);
				this.sortTable(skills, 'vertical');
				this.addRowspan(skills);
				return skills;
			},
		},
	}

	export default {
		mixins: [ common_sort, sort, common_members, members, skills ],
		props: [
			'originMembers',
			'originSkills'
		],
		computed: {
			...mapState('skillTableFilter', {
				selectedAnnual             : 'annual',
				selectedTeam               : 'team',
				selectedSkillLargeCategory : 'skillLargeCategory',
				selectedSkillSmallCategory : 'skillSmallCategory',
			}),
			members() {
				return this.originMembers.slice();
			},
			skills() {
				return this.originSkills.slice();
			},
		},
	}
</script>