<template>
	<create-form-wrapper>
		<create-form
			:skill-large-categories="skillLargeCategories"
			:skill-small-categories="skillSmallCategories"
		></create-form>
	</create-form-wrapper>
</template>

<script>
	import { mapActions } from 'vuex'
	import store from 'store/page/skillmap/form/create/skill'
	import CreateFormWrapper from 'components/parts/skillmap/form/create/_createFormWrapper'
	import CreateForm        from 'components/parts/skillmap/form/create/skill/_createForm'
	export default {
		store,
		components: {
			CreateFormWrapper,
			CreateForm,
		},
		data() {
			return {
				skillLargeCategories : [],
				skillSmallCategories : [],
			}
		},
		mounted() {
			this.get({
				url: '/front/skillmap/form/create/skill',
				success: (res) => {
					this.skillLargeCategories = res.skillLargeCategories;
					this.skillSmallCategories = res.skillSmallCategories;
				}
			});
		},
		methods: {
			...mapActions('api', [
				'get'
			]),
		},
	}
</script>