let types = {
	skill         : 'setSkill',
	certification : 'setCertification',
}
export default {
	namespaced: true,
	state: {
		isShowSkillTable         : true,
		isShowCertificationTable : false,
	},
	mutations: {
		[types.skill] (state, value) {
			state.isShowSkillTable = value;
		},
		[types.certification] (state, value) {
			state.isShowCertificationTable = value;
		},
	},
	actions: {
		setSkill(context, value) {
			context.commit(types.skill, value)
		},
		setCertification(context, value) {
			context.commit(types.certification, value)
		},
	},
}