<template>
	<div class="filterBar">
		<div class="filterBar__title">絞り込み条件</div>
		<select
			v-model="filterSkillLargeCategoryId">
			<option value="" selected>未選択</option>
			<option
				v-for="category in skillLargeCategories"
				:value="category.id"
			>{{ category.name }}</option>
		</select>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex'
	export default {
		props: ['skillLargeCategories'],
		methods: {
			...mapActions('filter', [
				'setSkillLargeCategoryId'
			]),
		},
		computed: {
			...mapState('filter', [
				'skillLargeCategoryId'
			]),
			filterSkillLargeCategoryId: {
				get() {
					return this.skillLargeCategoryId
				},
				set(value) {
					this.setSkillLargeCategoryId(value)
				}
			},
		}
	}
</script>