let types = {
	skillLargeCategoryId: 'setSkillLargeCategoryId',
}
export default {
	namespaced: true,
	state: {
		skillLargeCategoryId : '',
	},
	mutations: {
		[types.skillLargeCategoryId] (state, value) {
			state.skillLargeCategoryId = value;
		},
	},
	actions: {
		setSkillLargeCategoryId(context, value) {
			context.commit(types.skillLargeCategoryId, value)
		},
	},
}