<script>
import { Bar, mixins } from 'vue-chartjs'
import { mapState } from 'vuex'

export default {
	extends: Bar,
	data: function() {
		return {
			options: {
				responsive: true,
				maintainAspectRatio: false,
				layout: {
					padding: 40,
				},
				title: {
					display: true,
					text: this.title,
				},
				scales: {
					xAxes: [
						{
							ticks: {
								autoSkip    : false,
								maxRotation : 90,
								minRotation : 90,
							}
						}
					],
				},
			},
		};
	},
	props: ['data', 'name', 'title', 'isFilter'],
	mounted: function() {
		this.updateData();
		this.renderChart(this.chartData, this.options)
	},
	watch: {
		filteredCurrentData: function() {
			this.updateData();
		},
	},
	methods: {
		updateData: function() {
			let filteredCurrentData = this.filteredCurrentData;
			let filteredFeatureData = this.filteredFeatureData;
			this.chartData = {
				'labels': filteredCurrentData.map((item) => { return item[this.name + '_name']; }),
				'datasets': [
					{
						'label'          : '現在',
						'maxBarThickness': 100,
						'borderColor'    : "rgba(160,160,250,1)",
						'borderWidth'    : 1,
						'backgroundColor': "rgba(160,160,250,0.6)",
						'data'           : filteredCurrentData.map((item) => { return item['average']; }),
					},
					{
						'label'          : '未来',
						'maxBarThickness': 100,
						'borderColor'    : "rgba(250,160,160,1)",
						'borderWidth'    : 1,
						'backgroundColor': "rgba(250,160,160,0.6)",
						'data'           : filteredFeatureData.map((item) => { return item['average']; }),
					},
				]
			}
		},
	},
	computed: {
		...mapState('filter', {
			filterSkillLargeCategoryId: 'skillLargeCategoryId'
		}),
		filteredCurrentData: function() {
			if (!this.isFilter) return this.data.current;
			return this.data.current.filter((item) => {
				if (!this.filterSkillLargeCategoryId) return true;
				return item.skill_large_category_id == this.filterSkillLargeCategoryId;
			})
		},
		filteredFeatureData: function() {
			if (!this.isFilter) return this.data.feature;
			return this.data.feature.filter((item) => {
				if (!this.filterSkillLargeCategoryId) return true;
				return item.skill_large_category_id == this.filterSkillLargeCategoryId;
			})
		}
	},
	mixins: [mixins.reactiveData],
}
</script>