<template>
	<div class="narrowCond">
		<div class="narrowCond__item">
			<table>
				<tr>
					<th colspan="2">列絞り込み</th>
				</tr>
				<tr>
					<td>年次</td>
					<td>
						<select
							class="narrowCond__item__select"
							v-model="selectAnnual"
						>
							<option value="">絞り込みなし</option>
							<option
								v-for="a in annuals"
								:value="a"
							>
								{{ a }}年度
							</option>
						</select>
					</td>
				</tr>
				<tr>
					<td>チーム</td>
					<td>
						<select
							class="narrowCond__item__select"
							v-model="selectTeam"
						>
							<option value="">絞り込みなし</option>
							<option
								v-for="t in teams"
								:value="t.id"
							>
								{{ t.name }}
							</option>
						</select>
					</td>
				</tr>
			</table>
		</div>
		<div class="narrowCond__item">
			<table>
				<tr>
					<th colspan="2">行絞り込み</th>
				</tr>
				<tr>
					<td>大カテゴリ</td>
					<td>
						<select
							class="narrowCond__item__select"
							v-model="selectSkillLargeCategory"
						>
							<option value="">絞り込みなし</option>
							<option
								v-for="lc in skillLargeCategories"
								:value="lc.id"
							>
								{{ lc.name }}
							</option>
						</select>
					</td>
				</tr>
				<tr>
					<td>小カテゴリ</td>
					<td>
						<select
							class="narrowCond__item__select"
							v-model="selectSkillSamllCategory"
						>
							<option value="">絞り込みなし</option>
							<option
								v-for="sc in skillSmallCategories"
								:value="sc.id"
							>
								{{ sc.name }}
							</option>
						</select>
					</td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex'
	export default {
		props: [
			'skillLargeCategories',
			'skillSmallCategories',
			'teams',
			'annuals'
		],
		methods: {
			...mapActions('skillTableFilter', [
				'setAnnual',
				'setTeam',
				'setSkillLargeCategory',
				'setSkillSmallCategory'
			]),
		},
		computed: {
			...mapState('skillTableFilter', [
				'annual',
				'team',
				'skillLargeCategory',
				'skillSmallCategory'
			]),
			selectAnnual: {
				get()  { return this.annual },
				set(v) { this.setAnnual(v) }
			},
			selectTeam: {
				get()  { return this.team },
				set(v) { this.setTeam(v) }
			},
			selectSkillLargeCategory: {
				get()  { return this.skillLargeCategory },
				set(v) { this.setSkillLargeCategory(v) }
			},
			selectSkillSamllCategory: {
				get()  { return this.skillSmallCategory },
				set(v) { this.setSkillSmallCategory(v) }
			}
		}
	}
</script>