let types = {
	annual             : 'setAnnual',
	team               : 'setTeam',
	skillLargeCategory : 'setSkillLargeCategory',
	skillSmallCategory : 'setSkillSmallCategory',
}
export default {
	namespaced: true,
	state: {
		annual             : '',
		team               : '',
		skillLargeCategory : '',
		skillSmallCategory : '',
	},
	mutations: {
		[types.annual] (state, value) {
			state.annual = value;
		},
		[types.team] (state, value) {
			state.team = value;
		},
		[types.skillLargeCategory] (state, value) {
			state.skillLargeCategory = value;
		},
		[types.skillSmallCategory] (state, value) {
			state.skillSmallCategory = value;
		},
	},
	actions: {
		setAnnual(context, value) {
			context.commit(types.annual, value)
		},
		setTeam(context, value) {
			context.commit(types.team, value)
		},
		setSkillLargeCategory(context, value) {
			context.commit(types.skillLargeCategory, value)
		},
		setSkillSmallCategory(context, value) {
			context.commit(types.skillSmallCategory, value)
		},
	},
}