<template>
	<div class="form">
		<div class="form__items">
			<div class="form__title">スキルを登録する</div>
			<div
				v-if="isSuccess"
				class="form__messaage --success"
			>登録に成功しました。</div>
			<div
				v-if="errors.length > 0"
				class="form__messaage --error"
			>
				<p v-for="errorMessage in errors">・{{ errorMessage }}</p>
			</div>
			<div class="form__item --table">
				<table>
					<tr>
						<td>
							<div class="form__label">
								<div>大カテゴリを選ぶ</div>
								<div class="form__required">必須</div>
							</div>
						</td>
						<td>
							<select
								v-model="skill_large_category_id"
								class="form__select"
							>
								<option disabled value="">大カテゴリを選択してください。</option>
								<option
									v-for="category in skillLargeCategories"
									:value="category.id"
								>{{ category.name }}</option>
							</select>
						</td>
					</tr>
					<tr v-if="skill_large_category_id">
						<td>
							<div class="form__label">
								<div>小カテゴリを選ぶ</div>
								<div class="form__required">必須</div>
							</div>
						</td>
						<td>
							<select
								v-model="skill_small_category_id"
								class="form__select"
							>
								<option disabled value="">小カテゴリを選択してください。</option>
								<option
									v-for="category in selectSkillSmallCategories"
									:value="category.id"
								>{{ category.name }}</option>
							</select>
						</td>
					</tr>
					<tr>
						<td>
							<div class="form__label">
								<div>スキル名を入力する</div>
								<div class="form__required">必須</div>
							</div>
						</td>
						<td>
							<input
								type="text"
								v-model="name"
								class="form__input"
								placeholder="スキル名"
							/>
						</td>
					</tr>
				</table>
			</div>
			<div class="form__item">
				<div
					@click="register"
					:class="{ '--disable' : !isInput }"
					class="form__submit"
				>登録する</div>
			</div>
		</div>
	</div>
</template>

<script>
	import formMixin from 'components/mixins/skillmap/form/create/form'
	export default {
		mixins: [ formMixin ],
		props: [
			'skillLargeCategories',
			'skillSmallCategories',
		],
		data() {
			return {
				formName: 'skill',
				formItems: [
					{
						name: 'name',
						required: 1
					},
					{
						name: 'skill_large_category_id',
						required:1
					},
					{
						name: 'skill_small_category_id',
						required:1
					}
				],
				name                   : '',
				skill_large_category_id: '',
				skill_small_category_id: '',
			}
		},
		computed: {
			selectSkillSmallCategories: function() {
				if (!this.skill_large_category_id) return [];
				return this.skillSmallCategories.filter((category) => {
					return this.skill_large_category_id == category.skill_large_category_id;
				});
			},
		},
	}
</script>