<template>
	<create-form-wrapper>
		<create-form
			:certification-categories="certificationCategories"
		></create-form>
	</create-form-wrapper>
</template>

<script>
	import { mapActions } from 'vuex'
	import store from 'store/page/skillmap/form/create/certification'
	import CreateFormWrapper from 'components/parts/skillmap/form/create/_createFormWrapper'
	import CreateForm        from 'components/parts/skillmap/form/create/certification/_createForm'
	export default {
		store,
		components: {
			CreateFormWrapper,
			CreateForm,
		},
		data() {
			return {
				certificationCategories: [],
			}
		},
		mounted() {
			this.get({
				url: '/front/skillmap/form/create/certification',
				success: (res) => {
					this.certificationCategories = res.certificationCategories;
				}
			});
		},
		methods: {
			...mapActions('api', [
				'get'
			]),
		},
	}
</script>